.conversation { 
  width: 311px;
  height: 72px;
  left: 32px;
  top: 431px;
  margin: 10px 0px 10px 0px;
  background: #FFFFFF;
  box-shadow: 0px 16px 32px rgba(36, 43, 53, 0.16);
  border-radius: 18px;
  } 
   
  .conversation:hover { 
    background-color: rgba(250, 250, 250, 0.89); 
  } 
   
  .conversationImg { 
    width: 40px; 
    height: 40px; 
    border-radius: 50%; 
    object-fit: cover; 
    margin: 5px 20px 0px 7px; 
  } 
   
  .conversationName { 
    font-weight: 500; 
  } 
   
  @media screen and (max-width: 768px) { 
    .conversationName { 
      display: none; 
    } 
  }
.messenger { 
    height: calc(100vh - 70px); 
    display: flex; 
  } 
   
  .chatMenu { 
    flex: 3.5 1; 
  } 
   
  .chatMenuInput { 
    width: 90%; 
    padding: 10px 0; 
    border: none; 
    border-bottom: 1px solid gray; 
  } 
   
  .chatBox { 
    flex: 5.5 1; 
  } 
   
  .chatBoxWrapper { 
    display: flex; 
    flex-direction: column; 
    justify-content: space-between; 
    position: relative; 
  } 
   
  .chatBoxTop { 
    height: 100%; 
    overflow-y: scroll; 
    padding-right: 250px; 
  } 
   
  .chatBoxBottom { 
    margin-top: 5px; 
    display: flex; 
    align-items: center; 
    justify-content: space-between; 
  } 
   
  .chatMessageInput { 
    width: 80%; 
    height: 90px; 
    padding: 10px; 
    border-radius: 20px; 
  } 
   
  .chatSubmitButton { 
    width: 70px; 
    height: 40px; 
    border: none; 
    border-radius: 20px; 
    cursor: pointer; 
    -webkit-filter: shadow(color=#999999, direction=135, strength=2); 
            filter: shadow(color=#999999, direction=135, strength=2);
    background-color: teal; 
    color: white; 
  } 
   
  .chatOnline { 
    flex: 30px 1; 
  } 
   
  .chatMenuWrapper, 
  .chatBoxWrapper, 
  .chatOnlineWrapper { 
    padding: 10px; 
    height: 100%; 
  } 
   
  .noConversationText { 
    position: absolute; 
    top: 10%; 
    font-size: 50px; 
    color: rgb(224, 220, 220); 
    cursor: default; 
  } 
   
  @media screen and (max-width: 768px) { 
    .chatMenu { 
      flex: 1 1; 
    } 
   
    .chatMenuInput { 
      display: none; 
    } 
   
    .chatBox{ 
      flex: 10 1; 
    } 
   
    .chatOnline{ 
      flex: 1px 1; 
    } 
  }
